<template>
  <div class="company">
    <div class="companyName">
      <label for=""></label>
      <input type="text" name="" v-model="companyName" placeholder="">
    </div>
    <div class="companyLogo">
      <label for="">LOGO</label>
      <div v-if="logoUrl === ''" class="addpic" @click="changePic('logo')"></div>
      <img v-else :src="logoUrl" alt="" class="logoImg">
      <span v-if="logoUrl !== ''" @click="changePic('logo')"></span>
    </div>
    <div class="companyLogo">
      <label for=""></label>
      <div v-if="xctpUrl === ''" class="addpic" @click="changePic('xctp')"></div>
      <img v-else :src="xctpUrl" alt="">
      <span v-if="xctpUrl !== ''" @click="changePic('xctp')"></span>
    </div>
    <div class="companyLogo">
      <label for=""></label>
      <div v-if="erwmUrl === ''" class="addpic" @click="changePic('erwm')"></div>
      <img v-else :src="erwmUrl" alt="">
      <span v-if="erwmUrl !== ''" @click="changePic('erwm')"></span>
    </div>
    <h3></h3>
    <div class="ad">
      <textarea name="name" placeholder="," v-model="ad"></textarea>
    </div>
    <div class="submit" @click="update"></div>
  </div>
</template>

<script>
  import axios from 'axios'
  import wx from 'weixin-js-sdk'
  export default {
    name: 'company',
    data () {
      return {
        companyName: '',
        logoId: '',
        xctpId: '',
        erwmId: '',
        logoUrl: '',
        xctpUrl: '',
        erwmUrl: '',
        logoReady: true,
        xctpReady: true,
        erwmReady: true,
        ad: '',
        id: null
      }
    },
    mounted: function () {
      this.companyMes()
    },
    methods: {
      companyMes: function () {
        var that = this
        axios.get('job/user/findCompanyByUserId', {
          params: {
            userId: that.$store.state.userId
          }
        }).then((res) => {
          console.log(res)
          var data = res.data.data
          that.companyName = data.name
          that.logoUrl = data.logo
          that.xctpUrl = data.publicity
          that.erwmUrl = data.QRCode
          that.ad = data.info
          that.id = data.id
        })
      },
      changePic: function (data) {
        var that = this
        wx.chooseImage({
          count: 1, // 9
          success: (res) => {
            switch (data) {
              case 'logo':
                that.logoReady = false
                that.logoId = res.localIds[0] // IDlocalIdimgsrc
                if (window.__wxjs_is_wkwebview) {
                  wx.getLocalImgData({
                    localId: that.logoId, // localID
                    success: (res) => {
                      that.logoUrl = res.localData // localDatabase64img
                    }
                  })
                } else {
                  that.logoUrl = that.logoId
                }
                wx.uploadImage({
                  localId: that.logoId, // IDchooseImage
                  isShowProgressTips: 1, // 1
                  success: (res) => {
                    axios.get('job/index/selectImage', {
                      params: {
                        mediaId: res.serverId
                      }
                    }).then((res) => {
                      that.logoUrl = res.data.urlPath
                      that.logoReady = true
                    })
                  }
                })
                break
              case 'xctp':
                that.xctpReady = false
                that.xctpId = res.localIds[0]
                if (window.__wxjs_is_wkwebview) {
                  wx.getLocalImgData({
                    localId: that.xctpId, // localID
                    success: (res) => {
                      that.xctpUrl = res.localData // localDatabase64img
                    }
                  })
                } else {
                  that.xctpUrl = that.xctpId
                }
                wx.uploadImage({
                  localId: that.xctpId, // IDchooseImage
                  isShowProgressTips: 1, // 1
                  success: (res) => {
                    axios.get('job/index/selectImage', {
                      params: {
                        mediaId: res.serverId
                      }
                    }).then((res) => {
                      that.xctpUrl = res.data.urlPath
                      that.xctpReady = true
                    })
                  }
                })
                break
              case 'erwm':
                that.erwmReady = false
                that.erwmId = res.localIds[0]
                if (window.__wxjs_is_wkwebview) {
                  wx.getLocalImgData({
                    localId: that.erwmId, // localID
                    success: (res) => {
                      that.erwmUrl = res.localData // localDatabase64img
                    }
                  })
                } else {
                  that.erwmUrl = that.erwmId
                }
                wx.uploadImage({
                  localId: that.erwmId, // IDchooseImage
                  isShowProgressTips: 1, // 1
                  success: (res) => {
                    axios.get('job/index/selectImage', {
                      params: {
                        mediaId: res.serverId
                      }
                    }).then((res) => {
                      that.erwmUrl = res.data.urlPath
                      that.erwmReady = true
                    })
                  }
                })
                break
              default:
            }
          }
        })
      },
      update: function () {
        if (this.logoReady === true && this.xctpReady === true && this.erwmReady === true) {
          var that = this
          axios.get('job/user/saveCompany', {
            params: {
              userId: that.$store.state.userId,
              userName: that.$store.state.userName,
              id: that.id,
              name: that.companyName,
              publicity: that.xctpUrl,
              QRCode: that.erwmUrl,
              info: that.ad,
              logo: that.logoUrl
            }
          }).then((res) => {
            alert('success!')
          })
        } else {
          alert(',...')
        }
      }
    }
  }
</script>

<style scoped>
  .company {
    flex: 1;
    background: #fafafa;
  }

  .companyName label {
    width: 99px;
    height: 14px;
    font-size: 14px;
    text-align: left;
    font-family: 'ArialMT', 'Arial';
    font-weight: 400;
    font-style: normal;
    color: #333333;
    padding-left: 12px;
    border-right: 1px solid #e4e4e4;
    line-height: 14px;
    margin-top: 9px;
  }

  .companyName {
    margin-top: 12px;
    border-top: 1px solid #e4e4e4;
    background: #fff;
    height: 32px;
    display: flex;
  }

  .companyName input{
    flex: 1;
    border: 0;
    padding-left: 16px;
  }

  .companyLogo {
    height: 78px;
    border-top: 1px solid #f5f5f5;
    background: #fff;
    display: flex;
    align-items: center;
  }

  .companyLogo label {
    width: 99px;
    height: 22px;
    font-size: 14px;
    text-align: left;
    font-family: 'ArialMT', 'Arial';
    font-weight: 400;
    font-style: normal;
    color: #333333;
    padding-left: 12px;
    border-right: 1px solid #e4e4e4;
    line-height: 22px;
  }

  .companyLogo .logoImg {
    width: 50px;
    height: 50px;
    border-radius: 100%;
  }

  .companyLogo img{
    margin-left: 22px;
    height: 44px;
  }

  .companyLogo span{
    font-family: 'PingFangSC-Regular', 'PingFang SC';
    font-weight: 400;
    font-style: normal;
    font-size: 12px;
    color: #999999;
    width: 85px;
    height: 23px;
    text-align: center;
    border: 1px solid #e9e9e9;
    line-height: 23px;
    border-radius: 6px;
    margin-left: 20px;
  }

  #imageFile{
    display: none;
  }

  .addpic{
    background: rgba(242, 242, 242, 1) url('../assets/u1310.png') no-repeat center center;
    width: 63px;
    height: 47px;
    margin-left: 16px;
  }

  h3{
    height: 44px;
    font-family: 'PingFangSC-Regular', 'PingFang SC';
    font-weight: 400;
    font-style: normal;
    font-size: 14px;
    text-align: left;
    color: #333333;
    line-height: 44px;
    border-top: 1px solid #e4e4e4;
    margin: 0;
    padding-left: 12px;
    background: #fff;
  }

  .ad{
    padding: 2px 12px 18px;
    display: flex;
    background: #fff;
  }

  .ad textarea{
    flex: 1;
    height: 105px;
    font-family: 'ArialMT', 'Arial';
    font-weight: 400;
    font-style: normal;
    font-size: 13px;
    text-decoration: none;
    text-align: left;
    border-color: rgb(169, 169, 169);
    border-width: 1px;
    border-style: solid;
    padding: 2px;
  }

  .submit {
    width: 90vw;
    height: 40px;
    background-color: rgba(255, 204, 0, 1);
    border-radius: 20px;
    font-size: 16px;
    color: #666666;
    line-height: 40px;
    font-family: 'ArialMT', 'Arial';
    font-weight: 400;
    font-style: normal;
    text-align: center;
    margin: 26px auto 0;
  }
</style>



