<template>
  <div id="app">
    <router-view/>
    <div id="alert" v-show="alert || alertSelect || loading || tishi">
      <img v-if="loading" src="./assets/loading.gif" alt="">
    </div>
    <div id="switchAlert" v-if="switchCountry">
      <div class="switch">
        <div class="switchTop">
          <span>{{lastLocation}}{{nowCountry}}</span>
        </div>
        <div class="switchButtom">
          <span @click="esc"></span>
          <span @click="choiceCountry"></span>
        </div>
      </div>
    </div>
    <mobile-select v-if="alertSelect" :status="mobileSelectStatus"></mobile-select>
  </div>
</template>

<script>
  import axios from 'axios'
  import wx from 'weixin-js-sdk'
  // import debug from 'm-debug'
  import {
    mapGetters,
    mapActions
  } from 'vuex'
  export default {
    name: 'app',
    data () {
      return {
        switchCountry: false,
        nowCountry: '',
        lastLocation: ''
      }
    },
    beforeMount: function () {
      axios.defaults.baseURL = this.$store.state.url
      this.isLogin()
      this.initwx()
    },
    computed: {
      ...mapGetters([ // storegetters
        'alert',
        'alertSelect',
        'activeYear',
        'activeMonth',
        'loading',
        'tishi',
        'mobileSelectStatus'
      ])
    },
    methods: {
      ...mapActions([ // storegetters
        'login', // src/store/getters.jsindex
        'actionCountry',
        'actionAlertSelect',
        'actionLoading',
        'activeUserHead',
        'activeUserName',
        'activeTel',
        'actionInterested'
      ]),
      isLogin: function () {
        if (localStorage._hbossUserId !== undefined) {
          this.login(localStorage._hbossUserId)
          this.userInfo()
        }
        if (this.$store.state.userId === '') {
          if (window.location.href.split('?userId=')[1] === undefined) {
            axios.get('weixin/config/oauth', {
              params: {
                'state': window.location.href + '?userId=okid'
              }
            }).then((res) => {
              window.location.href = res.data.data
            }).catch((err) => {
              console.log(err)
            })
          } else {
            localStorage._hbossUserId = window.location.href.split('?userId=')[1]
            this.login(localStorage._hbossUserId)
            this.userInfo()
          }
        }
      },
      initwx: function () {
        var that = this
        let nowUrl = window.location.href.split('#')[0]
        axios.get('weixin/config/jsConfig', {
          params: {
            url: nowUrl
          }
        }).then((res) => {
          var data = res.data.data
          wx.config({
            debug: false, // ,apialertpclogpc
            appId: data.appId, // 
            timestamp: data.timestamp, // 
            nonceStr: data.nonceStr, // 
            signature: data.signature, // 1
            jsApiList: [
              'onMenuShareTimeline',
              'onMenuShareAppMessage',
              'chooseImage',
              'uploadImage',
              'startRecord',
              'stopRecord',
              'onVoiceRecordEnd',
              'uploadVoice',
              'getLocalImgData',
              'getLocation'
            ] // JSJS2
          })
          wx.ready((res) => {
            that.actionLoading(false)
            wx.getLocation({
              type: 'wgs84', // wgs84gpsopenLocation'gcj02'
              success: function (res) {
                axios.get('https://maps.googleapis.com/maps/api/geocode/json', {
                  params: {
                    latlng: res.latitude + ',' + res.longitude,
                    key: 'AIzaSyDofWG3q9axLTVA7rbJka3E6Ahh14NOfos'
                  }
                }).then((res) => {
                  that.nowCountry = res.data.results[res.data.results.length - 1].formatted_address
                  if (that.nowCountry === '' || that.nowCountry === '' || that.nowCountry === '' || that.nowCountry === '' || that.nowCountry === '') {
                    that.switchCountry = true
                  }
                })
              }
            })
          })
          wx.error((res) => {
            // configerrorconfigdebugresSPA
            history.go(0)
          })
        })
      },
      userInfo: function () {
        var _this = this
        axios.get('job/user/getUserInfoByUserId', {
          params: {
            userId: _this.$store.state.userId
          }
        }).then(function (res) {
          var data = res.data.user
          if (res.status === 200) {
            _this.lastLocation = data.lastLocation
            _this.actionCountry(data.lastLocationId)
            _this.activeUserHead(data.head)
            _this.activeUserName(data.name)
            _this.activeTel(data.tel)
            _this.actionInterested(data.interested)
          }
        })
      },
      esc: function () {
        this.switchCountry = false
      },
      choiceCountry: function () {
        var _this = this
        switch (this.lastLocation) {
          case '':
            this.$store.state.country = 1
            break
          case '':
            this.$store.state.country = 2
            break
          case '':
            this.$store.state.country = 3
            break
          case '':
            this.$store.state.country = 4
            break
          case '':
            this.$store.state.country = 5
            break
          default:
        }
        this.switchCountry = false
        axios.get('job/user/getUserInfoByUserId', {
          params: {
            userId: _this.$store.state.userId,
            countriesId: _this.$store.state.country
          }
        })
      }
    }
  }
</script>

<style>
  html,
  body,
  #app {
    width: 100%;
    height: 100%;
    margin: 0;
    padding: 0;
  }

  ul,
  li {
    margin: 0;
    padding: 0;
    list-style:none;
  }

  img,p {
    margin: 0;
  }

  a {
    text-decoration: none;
  }

  .body {
    background: #f2f2f2;
  }

  #alert {
    position: fixed;
    left: 0px;
    top: 0px;
    width: 100%;
    height: 100%;
    background-color: rgba(36, 36, 36, 1);
    opacity: 0.607843;
    z-index: 100;
    display: flex;
    justify-content: center;
    align-items: center;
  }

  #switchAlert {
    position: fixed;
    left: 0px;
    top: 0px;
    width: 100%;
    height: 100%;
    background-color: rgba(36, 36, 36, 0.607843);
    z-index: 100;
    display: flex;
    justify-content: center;
    align-items: center;
  }

  #alert img{
    width: 100%;
  }

  .switch {
    width: 300px;
    height: 118px;
    background-color: rgba(255, 255, 255, 1);
    border-radius: 5px;
    display: flex;
    flex-direction: column;
    align-items: center;
  }

  .switchTop {
    flex: 1;
    text-align: center;
    display: flex;
    justify-content: center;
    align-items: center;
  }

  .switchTop span{
    width: 90%;
  }

  .switchButtom {
    width: 300px;
    height: 42px;
    font-family: 'ArialMT', 'Arial';
    font-weight: 400;
    font-style: normal;
    font-size: 15px;
    color: #333333;
    text-align: center;
    display: flex;
    border-top: 1px solid #f2f2f2;
    line-height: 42px;
  }

  .switchButtom span{
    flex: 1;
  }

  .switchButtom span:last-child{
    border-left: 1px solid #f2f2f2;
    color: #ffd633;
  }
</style>



